import { useBriefConfigForm } from "@rp/react/common/brief"
import { BriefStepsLegend, VerticalScrollStep } from "@rp/react/web/common/composite-components"
import { TextField, Typography } from "@rp/react/web/mui/core"
import { LabelOutlined } from "@rp/react/web/mui/icons"
import { BriefStepsContent } from "../BriefStepsContent"

type Props = {
  isLoadingWatchlistPreload: boolean
}

const BriefSaveStep = ({ isLoadingWatchlistPreload }: Props) => {
  const {
    form: { name },
    setName,
  } = useBriefConfigForm()

  return (
    <VerticalScrollStep>
      <BriefStepsLegend>
        <Typography>Give your Brief a name:</Typography>
      </BriefStepsLegend>
      <BriefStepsContent Icon={LabelOutlined} iconText="Name">
        <TextField
          sx={{ mt: 2 }}
          variant="outlined"
          fullWidth
          size="small"
          inputProps={{
            role: "input",
            "aria-label": "brief name",
          }}
          value={name}
          onChange={e => setName(e.target.value)}
          aria-disabled={isLoadingWatchlistPreload}
          disabled={isLoadingWatchlistPreload}
        />
      </BriefStepsContent>
    </VerticalScrollStep>
  )
}

export { BriefSaveStep }
